// Specific scss file for card content
@import '../../utils/globals_styles.scss';

.card {
  border-radius: 10px;
  background-position: center;
  background-size: cover;
  aspect-ratio: 1 / 1;
  position: relative;
  width: 340px;
  @media (max-width: $mobile) {
    height: 255px;
    aspect-ratio: unset;
    width: 100%;
  }
  &::before {
    content: '';
    background: $darken;
    border-radius: inherit;
    position: absolute;
    width: 100%;
    height: 100%;
    transition: background 0.3s ease;
  }
  &:hover {
    box-shadow: rgba(0, 0, 0, 0.25) 0px 4px 12px;
    .card__title {
      color: $redkasa;
    }
    &::before {
      background: $darken__hover;
    }
  }
  &__title {
    position: absolute;
    bottom: 0;
    margin: 20px;
    font-size: 18px;
    color: $white;
  }
}
